@import 'fonts.scss';

header{
    font-size: 10px;
  }
  .first-nav{
    width: 100%;
    min-height: 45px;
    background-color: #333333;
    font-family: $Proxima;
    font-weight: 700;
    color: #777777;
    font-size: 1.5em;
  }
  .fnav-wrapper{
    max-width: 1170px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: auto;
  }
  .currency{
    width: auto;
    i{
      padding-left: 0.4em;
    }
    #currency-type:hover{
      transition: color 0.2s ease;
      color: #b8b8b8;
    }
  }
  .user-interaction-menu{
    width: auto;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .user-account-buttons{
    display: flex;
    justify-content: space-between;
    a:nth-child(2) {
     padding-left: 2em;
     }
     a:hover{
      transition: color 0.2s ease;
      color: #b8b8b8;
    }
  }
  #shopping-cart{ 
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    height: 3em;
    width: 10em;
    margin: 0 0 0 3em;
    background-color: #00c8c8 ;
    #cart-status{
      padding-left: 1.5em;
      padding-right: 0.3em;
    }
  }
  .second-nav{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 110px;
    background-color: #f8f8f8;
    font-family: $Montserrat;
    font-size: 1.3em;
    font-weight: 500;
  }
  .secondNav-wrapper{
    width: 100%;
    margin: 0 5%;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
  }
  .logo {
    display: none;
    width: auto;
    font-size: 2.4em;
    padding: 0 0 0 0;
    color: rgb(34, 34, 34);
    font-weight: 600;
    text-transform: uppercase;
    cursor: default;
    #fashion{
      font-weight: 300;
    }
  }
  .goods-categories{
    position: relative;
    width: 100%;
    font-weight: 500;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    a{
      color: #222222;
      text-transform: uppercase;
      text-decoration: none;
      padding: 0 2.6em 0 0.5em;
    }
    .menu-link{
      display: inline-block;
      text-align: left;
      &:hover{
        color: #40cfcf;
      }
      i{
        position: absolute;
        top: 1.2em;
      }
    }
    .goods-search{
      position: relative;
        vertical-align: middle;
        white-space: nowrap;
      padding: 0 0 0 0;
      margin-left: 5%;
      input#search{
        outline: none;
        height: 30px;
        width: 190px;
        border: 1px solid #e7e7e7;
        background-color: #f8f8f8;
        font-size: 1.2em;
        padding-left: 0.7em;
        input:placeholder-shown{
          color: #989898;
          padding-left: 10px;
        }
        &::-webkit-search-cancel-button,
        ::-webkit-search-results-button,
        ::-webkit-search-results-decoration { display: none; } 
      }
        .search-icon{
          position: absolute;
          cursor: pointer;
          left: 88%;
          top: 25%;
          z-index: 1;
          font-size: 1.1em;
          color: rgb(152, 152, 152);
        }
    }
  }
  .dropdown {
    position: relative;
    display: inline-block;
  }
  .dropdown-content {
    display: none;
    position: absolute;
    top: 2.3em;
    width: 25vw ;
    height: 55vh;
    background-color: rgb(248, 248, 248);
    box-shadow: 0px 17px 24px 0px rgba(0, 0, 0, 0.14);
    z-index: 1;
    ul{
      list-style: none;
      padding: 1em 0 3em 0;
      li{
        font-size: 1em;
        font-weight: 400;
        color: #727272;
        line-height: 2.143; 
        &:hover{
          color: rgb(51, 51, 51);
          cursor: default;
        }
      }
    }
  }
  .dropdown:hover .dropdown-content {
    display: flex;
    flex-direction: column;
    padding: 2.7em 2.7em;
  }
  .dropdown:hover .menu-link {
    color: #40cfcf;
  }
  .clothes{
    display: flex;
    justify-content: space-between;
    padding:0 25% 0 0%;
    #casuals-title {
      font-size: 1em;
      font-weight: 500;
      color: #625c5e;
      line-height: 1.667;
    }
    #formal-title{
      font-size: 1em;
      font-weight: 500;
      color: #625c5e;
      line-height: 1.667;
    }
  }
  .season-sale {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgb(255, 255, 255);
    background-color: rgb(51, 51, 51);
    width: 100%;
    height: 100%;
    font-size: 2.5em;
    line-height: 1.333;
    #season-sale-words{
      font-weight: 600;
    }
    #season-discount-amount{
      font-weight: 400;
    }
  }
  //Footer
  footer{
    width: 100%;
    height: 100%;
    background-color: rgb(248, 248, 248);
    font-family: $Montserrat;
    font-size: 10px;
    .footer-wrapper{
      max-width: 1170px;
      display: flex;
      flex-direction: column;
      padding: 4% 0 0 0;
      margin: auto;
    
      .footer-information-content{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
      
        .footer-card{
          display: flex;
          flex-direction: column;
          text-align: left;
          margin: 0 1% 5% 0;
          
          .footer-content-title{
            color: rgb(87, 81, 83);
            font-size: 1.2em;
            line-height: 1.538;
            font-weight: 600;
            text-transform: uppercase;
          }
      
          .footer-card-body{
            display: flex;
            flex-direction: column;
            padding-top: 15%;
      
            a{
              font-size: 1.3em;
              line-height: 2.143;
              color: rgb(114, 114, 114);  
              text-decoration: none;
              &:hover{
                transition: color 0.2s ease;
                color: rgb(51, 51, 51);
              }
            }
          }
        }
      }
    
    .footer-blocks-content{
        display: flex;
        flex-direction: row;
        flex: 1 1 auto;
        flex-wrap: wrap;
        justify-content: space-between;
        margin: 0 0 5.5% 0;
    
        .footer-banner-award-winner{
          background-color: rgb(51, 51, 51);
          width: 49%;
          height: 20em;
          display: flex;
          flex-direction: column;
          justify-content: center;
          text-align: center;
    
          span{
            font-size: 3.5em;
            text-transform: uppercase;
            color: white;
          }
          #footer-banner-award-text-top{
            font-weight: 700;
          }
          #footer-banner-award-text-bottom{
            font-weight: 400;
          }
        }
        .footer-banner-social-media{
          background-color: rgb(0, 200, 200);
          display: flex;
          justify-content: space-evenly;
          align-items: center;
          width: 49%;
          height: 20em;
        
          a {
            i{ 
            font-size: 3.5em;
            color: white;
            &:hover{
              transition: color 0.2s ease-out;
              color: rgb(51, 51, 51);
            }
          }
        }
      }
    }
    } 
    .footer-basement{
      background-color: #333333;
      color: #777777;
      min-height: 45px;
      .footer-basement-wrapper{
        max-width: 1170px;
        display: flex;
        min-height: 45px;
        justify-content: space-between;
        text-align: center;
        align-items: center;
        margin: 0 auto;
        span{
        display: inline-block;
        font-size: 1.5em;
        font-weight: 600;
        }
      }
    }
  }

  @media screen and(min-width:1170px){
    .logo{
      display: block;
      }
  }
  
  @media screen and(max-width:1170px){
    .goods-search{
      display: none;
    }
    .fnav-wrapper{
      width: 85%;
      justify-content: space-between;
    }
    .goods-categories{
      justify-content: space-between;
    }
  }
  @media screen and(max-width:955px){
    
  }